import namespace from './namespace';
import {NAMESPACE as APP_NAMESPACE} from './../app';
import defaultMappingNamespace from './../mappings/namespace'
import {mapUsersByEmailResultEnum} from './enums/mapUsersByEmailResultEnum'

let service = {};
export default {
    async [namespace.actions.GET_DEFAULT_VALUES]({commit, dispatch}) {
        try {
            commit(namespace.mutations.SHOW_LOADER, true);
            commit(namespace.mutations.SET_DEFAULT_VALUES, await service.getDefaultValues());
            commit(namespace.mutations.SHOW_LOADER, false);
        } catch (err) {
            dispatch(namespace.actions.HANDLE_ERROR, err);
        }
    },
    async [namespace.actions.SET_AVAILABLE_VALUES]({commit, dispatch}, values) {
        try {
            commit(namespace.mutations.SHOW_LOADER, true);
            commit(namespace.mutations.SET_AVAILABLE_VALUES, values);
            commit(namespace.mutations.SHOW_LOADER, false);
        } catch (err) {
            dispatch(namespace.actions.HANDLE_ERROR, err);
        }
    },
    async [namespace.actions.UPDATE_DEFAULT_VALUES]({commit, state, dispatch}, defaultValues) {
        try {
            commit(namespace.mutations.SHOW_LOADER, true);
            await service.updateDefaultValues(defaultValues);
            commit(namespace.mutations.SHOW_LOADER, false);
        } catch (err) {
            dispatch(namespace.actions.HANDLE_ERROR, err);
        }
    },
    [namespace.actions.SET_UP_SERVICE]({}, payload) {
        if (payload.service !== undefined) {
            service = payload.service;
        }
        if (payload.prefix !== undefined) {
            service.backendUrl = payload.prefix;
        }
    },
    [namespace.actions.HANDLE_ERROR]({commit, dispatch}, error) {
        commit(namespace.mutations.SHOW_LOADER, false);
        dispatch(APP_NAMESPACE.actions.HANDLE_ERROR, error, {root: true});
    },
    async mapByEmail({commit, state, dispatch}, mappings) {
        try {
            commit(`mappings/${defaultMappingNamespace.mutations.SHOW_LOADER}`, true, {root: true});
            const mapUsersByEmailResponse = await service.mapByEmail(state[namespace.state.AVAILABLE_VALUES], mappings);
            let mapUsersByEmailResult = mapUsersByEmailResultEnum.SUCCESS;
            commit(`mappings/SET_MAPPINGS_VALUES`, {
                available: mapUsersByEmailResponse.available,
                mappings: mapUsersByEmailResponse.mappings
            }, {root: true});
            if (mapUsersByEmailResponse.mappedUsersCounter === 0) {
                mapUsersByEmailResult = mapUsersByEmailResultEnum.WARNING;
            }
            commit('setUsersMappingByEmailResult', {
                status: mapUsersByEmailResult,
                mappedUsersCounter: mapUsersByEmailResponse.mappedUsersCounter
            });
            dispatch('fireEmailMappingFlagTimeout');
        } catch (err) {
            dispatch(namespace.actions.HANDLE_ERROR, err);
        } finally {
            commit(`mappings/${defaultMappingNamespace.mutations.SHOW_LOADER}`, false, {root: true});
        }
    },
    async fireEmailMappingFlagTimeout({commit}) {
        return new Promise(resolve => {
            setTimeout(() => {
                commit('hideMappedByEmailFlag')
            }, 4000);
        });
    }
};


